.customer-template {
  @extend %max-width-container;
  padding: $global-spacing-unit-lg $global-spacing-unit-lg ($global-spacing-unit-lg * 2);
  header {
    border-bottom: 1px solid $color-grey-light;
    margin-bottom: $global-spacing-unit;
  }
}

.customer-form {
  .form-title {
    margin-bottom: $global-spacing-unit;
    font-weight: bold;
  }
  .form-subtitle {
    margin-bottom: $global-spacing-unit-sm;
  }
  .form-submit {
    font-weight: bold;
  }
}

.form-note {
  border-radius: $global-radius;
  padding: $global-spacing-unit-sm;
  margin-bottom: $global-spacing-unit;
  border: 1px solid transparent;
  font-size: 14px;
  text-align: left;
  width: 100%;
  &.is-success {
    color: $color-success;
    background-color: $color-success-alt;
    border-color: $color-success;
  }
  &.is-error {
    color: $color-error;
    background-color: $color-error-alt;
    border-color: $color-error;
  }
}
